* {
    margin: 0;
    padding: 0;
}

ul,
li,
ol {
    list-style: none;
}

a {
    text-decoration: none;
    color: #000;
}

@import url('https://fonts.googleapis.com/css2?family=Edu+TAS+Beginner&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Exo+2&display=swap');

.App {
    width: 1920px;
    height: 963px;
    font-family: 'Edu TAS Beginner', cursive;

    #wrap {
        width: 100%;
        height: 100%;
        background-color: #171828;
        position: relative;

        .main_top {
            width: 100%;
            height: 65px;
            // background-color: aquamarine;

            .row {
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: space-between;
                // background-color: aliceblue;

                .col-md-1 {
                    height: 70%;
                    color: #DBDAEF;
                    font-weight: bold;
                    font-size: 20px;
                    // background-color: rgb(138, 138, 138, 0.5);
                    display: flex;
                    align-items: center;
                    margin-left: 10px;
                    margin-right: 10px;

                    h1 {
                        width: 80%;
                        max-width: 480px;
                        margin-left: 5px;
                        margin-top: 5px;
                        font-family: 'Exo 2', sans-serif;
                        font-size: 1.3em;
                        line-height: 1.4em;
                        font-weight: 700;
                        color: #f3f3f3;
                      }
                      
                      /* Underline */
                      .underline {
                        line-height: 0.5em; /* Positive value. */
                        vertical-align: 0.5em; /* Positive value. */
                        border-bottom: .3em solid transparent;
                        -moz-border-image: -moz-linear-gradient(left, #A8DBED 0%, #E291E6 100%);
                        -webkit-border-image: -webkit-linear-gradient(left, #A8DBED 0%, #E291E6 100%);
                        border-image: linear-gradient(to right, #A8DBED 0%, #E291E6 100%);
                        border-image-slice: 1;
                      }
                      
                      .underline span {
                        vertical-align: -0.2em;
                      }
                      
                      /* Highlight */
                      *::selection {
                        background: rgba(226,145,230,.2);
                      }

                    & img {
                        width: 50px;
                        float: left;
                    }
                }
            }
        }

        .main_center {
            width: 100%;
            height: 800px;
            // background-color: rgb(138, 138, 138, 0.5);

            .row {
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: space-between;

                .col-md-1 {
                    width: 10%;
                    height: 100%;
                    display: flex;
                    align-items: center;

                    // background-color: rgba(112, 112, 112, 0.7);
                    .list {
                        margin-left: 10px;

                        li {
                            margin: 0 0 30px 10px;
                            font-size: 24px;
                            font-weight: bold;

                            a {
                                color: #e2e1f1;
                            }
                        }
                    }
                }

                .col-md-8 {
                    height: 100%;
                    display: flex;
                    align-items: flex-end;
                    // background-color: rgba(163, 163, 163, 0.7);

                    .main_box {
                        width: 880px;
                        height: 730px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        position: relative;

                        .window_box {
                            .window_wrap {
                                position: relative;

                                .moon {
                                    position: absolute;
                                    top: 57%;
                                    right: -12%;
                                    & img {
                                        width: 100px;
                                        position: absolute;
                                        z-index: 6;
                                    }
                                }

                                .like {
                                    position: absolute;
                                    bottom: -8%;
                                    left: -78%;

                                    & img {
                                        width: 60px;
                                        transform: rotate(-10deg);
                                    }
                                }

                                .document {
                                    position: absolute;
                                    top: 16%;
                                    left: -110%;

                                    & img {
                                        width: 160px;
                                    }
                                }

                                .star2 {
                                    position: absolute;
                                    top: -6%;
                                    right: 32%;

                                    & img {
                                        width: 50px;
                                    }
                                }

                                .illustration-editor {
                                    margin-top: -10rem;
                                    margin-right: -10rem;
                                    display: grid;
                                    grid-template-rows: 24px auto;
                                    justify-items: center;
                                    position: relative;
                                    z-index: 6;
                                  }
                                  
                                  .code {
                                    position: absolute;
                                    width: 290px;
                                    height: 145px;
                                    background: #4a4c554f;
                                    box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.5);
                                    border-radius: 6px;
                                    z-index: 5;
                                    header {
                                      display: grid;
                                      grid-template-columns: 36px auto 36px;
                                      align-items: center;
                                      
                                      svg {
                                        margin: 10px;
                                      }
                                      h1 {
                                        font-family: Lato, sans-serif;
                                        font-weight: 900;
                                        font-size: 14px;
                                        letter-spacing: 1.57px;
                                        color: #C5C8D4;
                                        margin: 0;
                                      }
                                    }
                                    &-html {
                                      left: -80px;
                                      top: 40px;
                                    }
                                    &-css {
                                      left: -140px;
                                      top: 200px;
                                      height: 155px;
                                      margin-top: 5px;
                                    }
                                    &-js {
                                      left: -35px;
                                      top: 360px;
                                      margin-top: 20px;
                                    }
                                    .code-content {
                                      margin: 0 10px 10px;
                                      color: white;
                                      .c-r { color: #A88038 }
                                      .c-o { color: #DE7300 }
                                      .c-y { color: #E1CA72 }
                                      .c-g { color: #74B087 }
                                      .c-p { color: #9F8198 }
                                      .c-b { color: #7A99AD }
                                      .c-c { color: #666666 }
                                    }
                                    &-css {
                                      .code-content code {
                                        .line {
                                          @for $i from 1 through 5 {
                                            &-#{$i} {
                                              width: 0%;
                                              margin: 0;
                                              border-right: .15em solid transparent;
                                              overflow: hidden;
                                              display: inline-block;
                                              white-space: nowrap;
                                            }
                                          }
                                          @for $i from 1 through 4 {
                                            &-#{$i} {
                                              animation:
                                                typing-#{$i} 3s unquote((($i - 1) * 3) + 's') steps(30, end) forwards,
                                                cursor 1s linear unquote((($i - 1) * 3) + 's') 3;
                                            }
                                          }
                                          &-1, &-3, &-4 {
                                            animation-duration: 1s;
                                            animation-timing-function: steps(10, end);
                                          }
                                          &-5 {
                                            border-color: transparent;
                                            animation:
                                              typing-5 3s 12s steps(30, end) forwards,
                                              cursor-persist 1s linear 12s infinite;
                                          }
                                          &-2 {
                                            margin-left: 2rem;
                                          }
                                          &-3, &-4, &-5 {
                                            margin-left: 4rem;
                                          }
                                        }
                                      }
                                    }
                                  }
                                  
                                  .editor-block {
                                    position: relative;
                                    display: block;
                                    width: auto;
                                    height: 421px;
                                    // background: linear-gradient(109.61deg, #4C4F5A 4.26%, #202125 84.84%);
                                    border-radius: 10px;
                                  }
                                  
                                  @keyframes typing-1 {
                                    from { width: 0 }
                                    to { width: 60px }
                                  }
                                  @keyframes typing-2 {
                                    from { width: 0 }
                                    to { width: 220px }
                                  }
                                  @keyframes typing-3 {
                                    from { width: 0 }
                                    to { width: 70px }
                                  }
                                  @keyframes typing-4 {
                                    from { width: 0 }
                                    to { width: 75px }
                                  }
                                  @keyframes typing-5 {
                                    from { width: 0 }
                                    to { width: 141px }
                                  }
                                  
                                  
                                  @keyframes cursor {
                                    from, to, 20%, 80% { border-color: transparent }
                                    25%, 75% { border-color: white }
                                  }
                                  
                                  @keyframes cursor-persist {
                                    from, 45% { border-color: transparent }
                                    50%, to { border-color: white }
                                  }
                            }
                        }

                        .laptop_box {
                            z-index: 6;
                            position: absolute;
                            right: 8%;
                            bottom: 17%;

                            .laptop_wrap {
                                position: relative;

                                    .cloud {
                                        & img {
                                            width: 260px;
                                            margin-left: -70px;
                                            margin-top: 10px;
                                            margin-bottom: -10px;
                                        }
                                    }
                                    .laptop {
                                        & img {
                                            width: 340px;
                                        }
                                    }

                                    .thumbs {
                                        position: absolute;
                                        bottom: 25%;
                                        right: 0;
                                        & img { 
                                            width: 70px;   
                                        }
                                    }

                                    .star {
                                        position: absolute;
                                        top: 30%;
                                        right: 15%;

                                        & img {
                                            width: 60px;
                                            transform: rotate(-15deg);
                                        }
                                    }

                                    .folder {
                                        position: absolute;
                                            bottom: -12%;
                                            left: -12%;

                                        & img {
                                            width: 140px;
                                            transform: rotate(10deg);
                                        }
                                    }
                                }
                           
                        }

                        .moon_wrap {
                            width: 28vw;
                            height: 28vw;
                            position: relative;

                            .moon {
                                width: 28vw;
                                background: rgb(104, 100, 180);
                                background: linear-gradient(0deg, rgba(104, 100, 180, 1) 0%, rgba(103, 77, 145, 1) 8%, rgba(77, 47, 111, 1) 21%, rgba(45, 33, 91, 1) 37%, rgba(40, 25, 78, 1) 50%, rgba(42, 17, 54, 1) 68%, rgba(28, 28, 28, 1) 90%);
                                height: 28vw;
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                border-radius: 50%;
                                font-size: 2rem;
                                color: white;
                                font-weight: bold;
                                position: relative;
                                z-index: 5;
                               
                            }

                            &::before,
                            &::after {
                                position: absolute;
                                content: "";
                                top: 17%;
                                left: 15%;
                                height: 67%;
                                width: 67%;
                                border-radius: 50%;
                                opacity: 0.4;
                                filter: blur(70px);
                            }

                            &::after {
                                background-color: #3462fc;
                                z-index: 2;
                                animation: backglow2 15s ease infinite;
                            }

                            &::before {
                                background-color: #ee54d4;
                                z-index: 1;
                                animation: backglow1 15s ease infinite;
                            }
                        }

                    }
                }

                @keyframes backglow1 {
                    0% {
                        transform: translate(10%, 10%) scale(1);
                        opacity: 0.5;
                    }

                    25% {
                        transform: translate(-10%, 10%) scale(0.9);
                        opacity: 0.5;
                    }

                    50% {
                        transform: translate(-10%, -10%) scale(1.2);
                        opacity: 0.8;
                    }

                    75% {
                        transform: translate(10%, -10%) scale(1);
                        opacity: 0.2;
                    }

                    100% {
                        transform: translate(10%, 10%) scale(1);
                        opacity: 0.5;
                    }
                }

                @keyframes backglow2 {
                    0% {
                        transform: translate(-10%, -10%) scale(1);
                        opacity: 0.5;
                    }

                    25% {
                        transform: translate(10%, -10%) scale(0.9);
                        opacity: 0.5;
                    }

                    50% {
                        transform: translate(10%, 10%) scale(1.2);
                        opacity: 0.8;
                    }

                    75% {
                        transform: translate(-10%, 10%) scale(1);
                        opacity: 0.2;
                    }

                    100% {
                        transform: translate(-10%, -10%) scale(1);
                        opacity: 0.5;
                    }
                }

                .my_tag {
                    width: 10%;
                    height: 100%;
                    position: relative;
                    // background-color: rgba(163, 163, 163, 0.7);

                    .tag_div {
                        width: 40px;
                        height: 150px;
                        background-color: #DBDAEF;
                        border-radius: 5px;
                        text-align: center;
                        position: fixed;
                        bottom: 12%;
                        right: 0;

                        .tag_point {
                            height: 30px;
                            background-color: #B0ADE1;
                            border-radius: 5px;
                        }
                    }

                }
            }
        }
    }
}
